.avatarItem {
    display: flex;
}

.aboutMe{
    width: 180px;
    text-align: center;
}

.button {
    appearance: none;
    -webkit-appearance: none;
    padding: 5px;
    border: none;
    background-color: #0404cb;
    color: #fff;
    font-weight: 600;
    border-radius: 5px;
    border: 2px solid black;
    width: 60px;
    cursor: pointer;
    opacity: 0.8;
}



.avatarItem img {
    width: 80px;
    height: 80px;
    position: relative;
    bottom: 20px;
    margin-right: 10px;
}

.avatarDescription {
    font-weight: 600;
    color: #000000;
}

.changePhoto {
    margin-bottom: 5px;
}

.contacts {
    margin-left: 5px;
}

.paddings {
    padding: 3px 0;
}

.block{
    display: grid;
    grid-template-columns: 150px 180px 300px;
}

